'use client';

/**
 * Format s·ªë ti·ªÅn th√†nh d·∫°ng 1,000,000 ‚Ç´
 * @param value - S·ªë ti·ªÅn c·∫ßn format
 * @returns Chu·ªói s·ªë ti·ªÅn ƒë√£ format
 */
export function formatCurrency(value: number | string, locale: string = 'vi-VN', currency: string = 'VND'): string {
  const number = typeof value === 'string' ? parseFloat(value) : value;

  if (isNaN(number)) return '0 ‚Ç´';

  return new Intl.NumberFormat(locale, {
    style: 'currency',
    currency,
    maximumFractionDigits: 0, // kh√¥ng hi·ªán s·ªë l·∫ª
  }).format(number);
}
